.main-banner .swiper{overflow: hidden; position: relative; margin: 40px 0 60px; padding-bottom: 34px; width: calc(100%);}
.main-banner .swiper .swiper-slide{overflow: hidden; position: relative; margin-right: 24px; width: 312px; height: 480px; border-radius: 24px;}
.main-banner .swiper .swiper-slide:last-of-type{margin-right: 0;}

.main-banner .swiper .swiper-slide.large{width: 872px;}
.main-banner .swiper .swiper-slide.large .tit{width: 58%; max-height: 150px; font-size: 40px; font-weight: 700;}
.main-banner .swiper .swiper-slide.large .desc{width: 58%; margin-top: 12px; font-size: 20px; line-height: 1.4;}
.main-banner .swiper .swiper-slide.large .sort{margin-bottom: 24px;}
//.main-banner .swiper .swiper-slide.large:hover .img-box{width: 310px; height: 310px;}
//.main-banner .swiper .swiper-slide.large .img-box{width: 290px; height: 290px;}

.main-banner .swiper .swiper-slide a{display: block; padding: 48px; width: 100%; height: 100%;}
.main-banner .swiper .swiper-slide .txt-box{position: relative; z-index: 2;}
.main-banner .swiper .swiper-slide .kind{@include flex(); margin-bottom: 20px; padding: 0 16px 0 12px; width: fit-content; height: 40px; font-size: 16px; font-weight: 500; color: #234281; border-radius: 20px; background-color: $c-white;}
.main-banner .swiper .swiper-slide .tit{overflow: hidden; text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 3; -webkit-box-orient: vertical; max-height: 90px; font-size: 24px; font-weight: 500; line-height: 1.25; color: $c-black; }
.main-banner .swiper .swiper-slide:hover .img-box{width: 120%; height: 120%;}
.main-banner .swiper .swiper-slide .img-box{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 100%; height: 100%; transform-origin: 0 0; transition: width 0.4s, height 0.4s;}
.main-banner .swiper .swiper-slide .img-box img{object-fit: cover;}

//.main-banner .swiper .swiper-slide .sort::before{content: ''; display: inline-block; margin-right: 6px; width: 24px; height: 24px; background-repeat: no-repeat; background-size: 100% auto; background-position: center;}
//.main-banner .swiper .swiper-slide .sort.fitness::before{background-image: url("/assets/img/common/ico_sort_fitness.svg");}
//.main-banner .swiper .swiper-slide .sort.class::before{background-image: url("/assets/img/common/ico_sort_class.svg");}
//.main-banner .swiper .swiper-slide .sort.employ::before{background-image: url("/assets/img/common/ico_sort_employ.svg");}
//.main-banner .swiper .swiper-slide .sort.insight::before{background-image: url("/assets/img/common/ico_sort_insight.svg");}

.main-banner .swiper .swiper-scrollbar{left: 50%; bottom: 0; transform: translateX(-50%); width: 648px; height: 2px; background-color: #D8DDE2; border-radius: 30px; transition: height 0.2s; z-index: 5;}
.main-banner .swiper .swiper-scrollbar:hover{height: 6px;}
.main-banner .swiper .swiper-scrollbar-drag{background-color: #2A4474;}

.main-white-box{padding: 48px 88px; background-color: $c-white; border-radius: 24px;}
.main-white-box + .main-white-box{margin-top: 32px;}
.main-white-box > .txt-box{margin-bottom: 32px;}
.main-white-box > .txt-box > .kind{@include flex(); margin-bottom: 20px; width: fit-content; font-size: 20px; font-weight: 700; color: $c-blue;}
.main-white-box > .txt-box > .kind::before{margin-right: 8px;}
.main-white-box > .txt-box > .tit{font-size: 32px; font-weight: 500; line-height: 1.19;}
.main-white-box > .txt-box > .desc{margin-top: 12px; font-size: 16px; color: #7C828B; line-height: 1.25;}
.main-white-box > .txt-box > .btn-group{justify-content: flex-start;}
.main-white-box > .btn-group{padding: 32px 0 0;}

.main-white-box.col{display: flex; }
.main-white-box.col > .txt-box{margin-right: 20px; margin-bottom: 0;}

.main-contents .thumb-list .swiper-wrapper{display: grid; grid-template-columns: 41.2% 1fr 1fr; gap: 32px 24px;}

.main-contents .thumb-list .swiper-slide.large{grid-row: 1/3;}
.main-contents .thumb-list .swiper-slide.large .img-box{border-width: 0;}
//.main-contents .thumb-list .swiper-slide.large .img-box::before{display: none;}
.main-contents .thumb-list .swiper-slide.large .img-box::before{padding-top: 110%;}
.main-contents .thumb-list .swiper-slide.large .tit{ max-height: 72px; font-size: 24px;}

.main-contents .thumb-list .swiper-slide{transform: translateY(0); transition: transform 0.3s;}
//.main-contents .thumb-list .swiper-slide:hover{transform: translateY(-10px);}
.main-contents .thumb-list .swiper-slide a{display: block;}
//.main-contents .thumb-list .swiper-slide .img-box{overflow: hidden; position: relative; height: 192px; border-radius: 16px; border: 1px solid #D8DDE2;}
.main-contents .thumb-list .swiper-slide .img-box{overflow: hidden; position: relative; border-radius: 16px; border: 1px solid #D8DDE2;}
.main-contents .thumb-list .swiper-slide .img-box::before{content: ''; display: block; padding-top: 61.5%;}
.main-contents .thumb-list .swiper-slide .img-box img{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); height: 100%; object-fit: cover;}
.main-contents .thumb-list .swiper-slide .sort{@include flex(); margin: 12px 0; font-size: 16px;}
.main-contents .thumb-list .swiper-slide .sort strong{@include flex(); font-weight: 500; color: $c-black;}
.main-contents .thumb-list .swiper-slide .sort strong::after{content: ''; display: inline-block; margin: 0 12px; width: 1px; height: 12px; background-color: #D8DDE2;}
.main-contents .thumb-list .swiper-slide .sort strong .icon{margin-right: 6px; width: 20px; height: 20px; background-color: transparent;}
.main-contents .thumb-list .swiper-slide .sort span{color: #7C828B;}
.main-contents .thumb-list .swiper-slide .tit{overflow: hidden;text-overflow: ellipsis; display: -webkit-box; max-height: 60px; -webkit-line-clamp: 2; -webkit-box-orient: vertical; font-size: 20px; font-weight: 500; line-height: 1.5; color: $c-black; text-align: left;}
.main-contents .thumb-list .swiper-slide .txt{display: flex; justify-content: space-between; align-items: center; margin-top: 13px; padding-top: 10px; font-size: 16px; border-top: 1px solid rgba(255, 255, 255, 0.1);}
.main-contents .thumb-list .swiper-slide .txt .status{font-weight: 700; color: #3575f6;}
.main-contents .thumb-list .swiper-slide .txt .period{color: #b9c0c6;}

.main-class{}
.main-class .thumb-list{overflow: hidden;}
.main-class .thumb-list .swiper-slide{flex: 1; margin-right: 24px; min-width: 268px;}
.main-class .thumb-list .swiper-slide:last-of-type{margin-right: 0;}
.main-class .thumb-list .img-box{overflow: hidden; position: relative; border-radius: 16px; border: 1px solid #D8DDE2;}
.main-class .thumb-list .img-box::before{content: ''; display: block; padding-top: 61.6%;}
.main-class .thumb-list .img-box img{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); }
.main-class .thumb-list .txt-box{}
.main-class .thumb-list .txt-box .sort{margin: 16px 0 12px; font-size: 16px;}
.main-class .thumb-list .txt-box .sort strong{font-weight: 500; color: $c-blue;}
.main-class .thumb-list .txt-box .sort strong::after{content: ''; display: inline-block; margin: 0 12px; width: 1px; height: 12px; background-color: #D8DDE2;}
.main-class .thumb-list .txt-box .sort span{color: #7C828B;}
.main-class .thumb-list .txt-box .tit{overflow: hidden; text-overflow: ellipsis; display: -webkit-box; max-height: 60px; -webkit-line-clamp: 2; -webkit-box-orient: vertical; font-size: 20px; font-weight: 500; line-height: 1.5; color: $c-black; text-align: left;}
.main-class .thumb-list .txt-box .hash-tag{margin-top: 6px;}
//.main-class .thumb-list .txt-box .hash-tag{ overflow: hidden; margin-top: 6px; text-overflow: ellipsis; white-space: nowrap;}
.main-class .thumb-list .txt-box .hash-tag span{margin-right: 10px; font-size: 16px; color: #7C828B; }
.main-class .thumb-list .txt-box .hash-tag span:last-child{margin-right: 0;}

.main-employ{}
.main-employ > .txt-box{flex-shrink: 0;}
.main-employ .thumb-list{overflow: hidden; margin-left: auto;}
.main-employ .thumb-list .swiper-slide{display: grid; grid-template-columns: 1fr 1fr; gap: 20px 24px;}
.main-employ .thumb-list .item{position: relative; border: 1px solid #D8DDE2; border-radius: 16px;}
.main-employ .thumb-list .item a{display: block; padding: 24px 20px 16px;}
.main-employ .thumb-list .item .badge{position: absolute; top: 25px; right: 24px;}
.main-employ .thumb-list .item .img-logo{overflow: hidden; width: 48px; height: 48px; border-radius: 8px; border: 1px solid #D8DDE2; background-color: #F4F5F8;}
.main-employ .thumb-list .item .txt-box{}
.main-employ .thumb-list .item .txt-box .sort{@include flex(); margin: 12px 0 8px; font-size: 16px;}
.main-employ .thumb-list .item .txt-box .sort strong{font-weight: 500; color: $c-blue;}
.main-employ .thumb-list .item .txt-box .sort strong::after{content: ''; display: inline-block; margin: 0 12px; width: 1px; height: 12px; background-color: #D8DDE2;}
.main-employ .thumb-list .item .txt-box .sort span{color: #2E3337;}
.main-employ .thumb-list .item .txt-box .tit{overflow: hidden; text-overflow: ellipsis; font-size: 20px; font-weight: 500; color: $c-black; white-space: nowrap;}
.main-employ .thumb-list .item .txt-box .desc{margin-top: 20px; padding-top: 12px; font-size: 14px; color: #7C828B; border-top: 1px solid #D8DDE2;}
.main-employ .thumb-list .item .txt-box .desc span::after{content: ''; display: inline-block; margin: 0 8px; width: 1px; height: 12px; background-color: #D8DDE2;}
.main-employ .thumb-list .item .txt-box .desc span:last-child::after{display: none;}

.main-employ .thumb-list .swiper-button{@include flex('flex-end'); margin-bottom: 16px;}
.main-employ .thumb-list .swiper-button > div{margin-top: 0; width: 36px; height: 36px;}
.main-employ .thumb-list .swiper-button .swiper-button-prev{position: initial; margin-right: 12px; @include bg('common/ico_circle_left.svg');}
.main-employ .thumb-list .swiper-button .swiper-button-next{position: initial; @include bg('common/ico_circle_right.svg');}

.main-partner{padding-top: 60px;}
.main-partner .banner{position: relative; overflow: hidden; margin-bottom: 24px; padding: 60px 86px 56px; border-radius: 24px;}
.main-partner .banner .txt-box{position: relative; z-index: 2;}
.main-partner .banner .txt-box .tit{margin-bottom: 24px; font-size: 24px; font-weight: 700;}
.main-partner .banner .img-box{position: absolute; top: 50%; transform: translateY(-50%);}

.main-partner .banner.employ-service{background-color: #DFE6F2;}
.main-partner .banner.employ-service .img-box{width: 290px; right: 16%;}

.main-partner .banner.info-service{position: relative; background-color: #15386F;}
.main-partner .banner.info-service::after{content: ''; display: block; position: absolute; top: 50%; right: 0; transform: translateY(-50%); width: 650px; height: 540px; @include bg('content/main_partner_obj_circle.png');}
.main-partner .banner.info-service .img-box{overflow: hidden; width: 171px; right: 22%; box-shadow: 0 20px 20px rgba(0, 0, 0, 0.16); border-radius: 42px; z-index: 1;}

.main-partner .partner-logo{display: grid; grid-template-columns: repeat(5, 15%); gap: 16px; padding: 74px 98px; justify-content: space-between;  background-color: #fff; border-radius: 24px;}
.main-partner .partner-logo li{overflow: hidden; position: relative; width: 100%; max-width: 156px;}
.main-partner .partner-logo li::before{content: ''; display: block; padding-top: 42%;}
.main-partner .partner-logo li img{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);}

@media all and (max-width:1400px) {
  //.main-white-box{padding: 48px;}
}


@media all and (max-width:1200px) {
  .main-white-box{padding: 48px;}

  .main-contents .thumb-list{overflow: hidden; margin-left: -48px; width: calc(100% + 96px); padding: 0 48px;}
  .main-class .thumb-list{overflow: hidden; margin-left: -48px; width: calc(100% + 96px); padding: 0 48px;}
  .main-employ .thumb-list{overflow: hidden; margin-left: -48px; width: calc(100% + 96px); padding: 0 48px;}
  //.main-contents .thumb-list{overflow: hidden; margin-left: -48px; width: calc(100% + 96px); padding: 0 48px;}

  .main-contents .thumb-list .swiper-wrapper{display: flex; gap: 0;}
  .main-contents .thumb-list .swiper-slide.large .img-box{border-width: 1px;}
  .main-contents .thumb-list .swiper-slide.large .img-box::before{padding-top: 61.5%;}
  .main-contents .thumb-list .swiper-slide.large .tit{ max-height: 60px; font-size: 20px;}

  .main-contents .thumb-list .swiper-slide{margin-right: 32px; width: 246px;}
  .main-contents .thumb-list .swiper-slide:last-child{margin-right: 0;}

  .main-employ{flex-wrap: wrap;}
  .main-employ .thumb-list .pc_only{display: none;}
  .main-employ .thumb-list .mo_only{display: block;}
  //.main-employ .thumb-list{width: 100%;}
  .main-employ .thumb-list .swiper-slide{display: flex; flex: 1; gap: 0; margin-right: 24px;}
  .main-employ .thumb-list .swiper-slide:last-child{margin-right: 0;}
}

@media all and (max-width:1080px) {
  //.main-contents .thumb-list .swiper-wrapper{grid-template-columns: 1fr 1fr;}
  //.main-contents .thumb-list .swiper-slide.large{grid-row: auto; grid-column: 1/-1;}
  //.main-partner .partner-logo{padding: 74px 60px;}
}

@media all and (max-width:960px) {
  .main-white-box{padding: 0;}

  .main-partner .partner-logo{ grid-template-columns: repeat(3, 156px); padding: 74px 40px;}
}


//fitness
.fitness{}
.fitness .sec-kv{position: relative; height: 1000px; @include bg('content/visual_fitness.jpg', $size: cover);}
.fitness .sec-kv .txt-box{position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); text-align: center;}
.fitness .sec-kv .txt-box .tit{font-size: 52px; font-weight: 700; color: $c-white;}
.fitness .sec-kv .txt-box .tit img{margin-top: 2px; width: fit-content;}
.fitness .sec-kv .txt-box .desc{margin-top: 24px; font-size: 28px; line-height: 1.2; color: $c-white; opacity: 0.8;}
.fitness .sec-kv .txt-box .btn{margin-top: 58px; padding: 0 32px 0 12px; height: 56px; font-size: 18px;}
.fitness .sec-kv .txt-box .btn .icon-play{margin-right: 16px; width: 32px; height: 32px; @include bg('common/ico_play2.svg');}
.fitness .sec-kv .notice-txt{position: absolute; bottom: 60px; left: 50%; transform: translateX(-50%); padding: 16px 24px; width: fit-content; border-radius: 36px; backdrop-filter: blur(6px); background-color: #fff;}
.fitness .sec-kv .notice-txt p{font-size: 24px; font-weight: 500;}
.fitness .sec-kv .notice-txt p::before{content: ''; display: inline-block; margin-right: 5px; width: 24px; height: 24px; @include mask('conmmon/ico_star.svg'); background-color: #D2DFF9;}

.fitness .sec-01{padding: 160px 0;}
.fitness .sec-01 .txt-list{}
.fitness .sec-01 .txt-list li{margin-bottom: 9px; font-size: 32px; font-weight: 700; color: #D2DBE3; letter-spacing: -2.5px;}
.fitness .sec-01 .txt-list li:last-child{margin-bottom: 0;}
.fitness .sec-01 .txt-list li span{background: radial-gradient(circle at 50% 50%, #fff, #359df6 35%, #6f779f 71%); color: transparent; -webkit-background-clip: text;}

.fitness .sec-02{padding: 140px 0; background-color: #e9edf8;}
.fitness .sec-02 .info-list{display: flex; align-items: stretch;}
.fitness .sec-02 .info-list .item{margin-right: 24px; padding: 24px 54px 40px; flex: 1; text-align: center; background-color: $c-white; border-radius: 20px;}
.fitness .sec-02 .info-list .item:last-child{margin-right: 0;}
.fitness .sec-02 .info-list .item .txt-box{}
.fitness .sec-02 .info-list .item .txt-box::before{content: ''; display: block; margin: 0 auto 32px; width: 198px; height: 141px; background-color: #ddd; background-repeat: no-repeat; background-position: center; background-size: contain;}
.fitness .sec-02 .info-list .item .txt-box.prepare{background-image: url("/assets/img/content/fitness_sec02_ico_prepare.svg");}
.fitness .sec-02 .info-list .item .txt-box.salary{background-image: url("/assets/img/content/fitness_sec02_ico_salary.svg");}
.fitness .sec-02 .info-list .item .txt-box.employment{background-image: url("/assets/img/content/fitness_sec02_ico_employment.svg");}

.fitness .sec-02 .info-list .item .txt-box span{font-size: 20px; font-weight: 500; color: #7c828b;}
.fitness .sec-02 .info-list .item .txt-box .num{font-size: 32px; font-weight: 700; color: #3d485f;}
.fitness .sec-02 .info-list .item .txt-box .num strong{font-size: 52px; font-weight: 900; color: #272e3b;}
.fitness .sec-02 .info-list .item .txt-box .num .icon-up{margin-left: 6px; width: 28px; height:28px; @include bg('common/ico_up.svg');}

.fitness .sec-02 .info-list .item .desc-box{margin-top: 20px; padding-top: 20px; border-top: 1px solid #d8dde2;}
//.fitness .sec-02 .info-list .item .desc-box::before{content: ''; display: block; width: 100%; height}
.fitness .sec-02 .info-list .item .desc-box .desc{font-size: 20px; color: #7c828b;}
.fitness .sec-02 .info-list .item .desc-box .desc strong{font-weight: 500; color: #2e3337;}
.fitness .sec-02 .info-list .item .desc-box .desc-small{margin-top: 6px; font-size: 16px; color: #b9c0c6;}

.fitness .sec-03{}
.fitness .sec-03 .banner{position: relative; height: 450px; @include bg('content/fitness_sec03_banner.jpg'); color: #fff; text-align: center;}
.fitness .sec-03 .banner .txt-box{position: absolute; left: 50%; bottom: 80px; transform: translateX(-50%);}
.fitness .sec-03 .banner .txt-box span{font-size: 31px; font-weight: 500; opacity: 0.8;}
.fitness .sec-03 .banner .txt-box p{margin-top: 2px; font-size: 54px; font-weight: 700;}

.fitness .sec-03 .service-01{padding-top: 120px;}
.fitness .sec-03 .service-01 .info-box{display: grid; grid-template-columns: 1fr 1fr; gap: 24px;}
.fitness .sec-03 .service-01 .info-box li{overflow: hidden; font-size: 24px; font-weight: 700; color: #141618; border-radius: 24px;}

.fitness .sec-03 .service-02{padding: 160px 0 368px;}
.fitness .sec-03 .service-02 .info-list{display: flex;}
.fitness .sec-03 .service-02 .info-list li{margin-right: 100px; font-size: 24px; font-weight: 500; color: #141618; text-align: center;}
.fitness .sec-03 .service-02 .info-list li::before{content: ''; display: block; margin: 0 auto 12px; width: 86px; height: 86px; background-color: #C4CDE8; background-repeat: no-repeat; background-position: center; background-size: contain; border-radius: 50%;}
.fitness .sec-03 .service-02 .info-list li:last-child{margin-right: 0;}

.fitness .sec-03 .service-02 .info-list li:nth-child(1)::before{background-image: url("/assets/img/content/");}
.fitness .sec-03 .service-02 .info-list li:nth-child(2)::before{background-image: url("/assets/img/content/");}
.fitness .sec-03 .service-02 .info-list li:nth-child(3)::before{background-image: url("/assets/img/content/");}
.fitness .sec-03 .service-02 .info-list li:nth-child(4)::before{background-image: url("/assets/img/content/");}




  //support
.support .content-box{min-height: 802px;}

.support .notice-table li + li{margin-top: 6px;}
.support .notice-table li a{@include flex(); padding: 12px 24px; font-size: 16px; border-radius: 12px;}
.support .notice-table li a:hover{background-color: $c-gray-2;}
.support .notice-table .tit{padding-right: 20px; font-weight: 500; color: $c-gray-3; line-height: 1.75;}
.support .notice-table .date{margin-left: auto; color: $c-gray-4; line-height: 1.25;}

.support .question .accordion{margin-bottom: 6px; padding: 0 24px;}
.support .question .accordion:last-child{margin-bottom: 0;}
.support .question .accordion.active{background-color: #f7f8fd; border-radius: 12px;}
.support .question .accordion.active .top{border-bottom: 1px solid $c-gray-6;}
.support .question .accordion.active .top button::after{transform: rotate(-180deg);}
.support .question .accordion .top button{@include flex(); position: relative; padding: 12px 0 12px 40px; width: 100%; font-size: 16px; line-height: 1.5;}
.support .question .accordion .top button::after{content: ''; display: inline-block; margin-left: auto; width: 24px; height: 24px; background-color: $c-gray-3; @include mask('common/icon_arrow_down.svg');}
.support .question .accordion .top button::before{content: ''; position: absolute; left: 0; top: 11px; width: 24px; height: 24px; background-color: $c-main; @include mask('common/icon_question.svg', $c-main)}
.support .question .accordion .top .kind{flex-shrink: 0; color: $c-gray-4; }
.support .question .accordion .top .kind::after{content: ''; display: inline-block; margin: -2px 12px 0; width: 1px; height: 18px; vertical-align: middle; background-color: $c-gray-6;}
.support .question .accordion .top .tit{font-weight: 500; color: $c-gray-3; text-align: left;}
.support .question .accordion .detail{padding: 20px 48px 24px 95px;}
.support .question .accordion .detail strong{display: inline-block; margin-bottom: 8px; font-size: 16px; font-weight: 500; color: $c-black; line-height: 1.25;}
