  .thumb-box-list .thumb-list .item .sort strong::after{margin: 0 3.33vw; width: 0.28vw; height: 3.33vw;}
  .thumb-box-list .thumb-list .item .sort strong .icon{margin-right: 1.67vw; width: 5.56vw; height: 5.56vw;}
  .thumb-box-list .thumb-list .item .tit{height: fit-content; font-size: 4.44vw;}
  .thumb-box-list .thumb-list .item .txt{margin-top: 3.33vw; padding-top: 3.33vw; font-size: 3.89vw; border-top-width: 0.28vw;}

  .thumb-box-list .thumb-list .item.complete-end .img-box::after{width: 36.11vw; height: 36.11vw;}
}

.contents.apply-form{max-width: 796px;}
.contents.apply-form .page-tit{margin-bottom: 32px;}
.contents.apply-form .form-con .content-box:first-child{margin-top: 0;}

.form-con .content-box + .content-box{margin-top: 20px;}
.form-con .title{display: flex; align-items: center; margin-bottom: 16px;}
.form-con .title h3{font-size: 20px; font-weight: 500;}
.form-con .title p{margin-left: 10px;}
.form-con .desc-box{@include flex(); margin-bottom: 16px; padding: 0 16px; height: 48px; background-color: #f4f5f8; border-radius: 12px;}
.form-con .notice-txt{position: relative; padding-left: 12px; font-size: 14px; color: #7c828b;}
.form-con .notice-txt::before{content: ''; display: block; position: absolute; left: 0; top: 9px; width: 3px; height: 3px; border-radius: 50%; background-color: #7c828b;}

.form-con .inp-list .inp-checkbox-group > .inp-label{margin-bottom: 13px;}
.form-con .inp-list .checkbox-list{margin-bottom: -13px;}
.form-con .inp-list .checkbox-list ~ .inp.etc{margin-top: 12px;}
.form-con .inp-list .inp-checkbox .inp-label{margin-bottom: 13px;}
.form-con .inp-list .inp-radio-group > .inp-label{margin-bottom: 13px;}
.form-con .inp-list .radio-list{margin-bottom: -13px;}
.form-con .inp-list .inp-radio .inp-label{margin-bottom: 13px;}
.form-con .inp-radio-list.type2 .inp-radio .inp-label{margin-bottom: 0;}

.form-con .inp-address .inp-group.pc_only{display: flex;}
.form-con .inp-address .inp-group .inp-select{flex: 1; margin-right: 12px;}
.form-con .inp-address .inp-group .inp-select:last-child{margin-right: 0;}
.form-con .inp.textarea .inp-label .counter{margin-left: auto; font-size: 14px; color: #7c828b;}
.form-con .inp.textarea .inp-area{height: 128px;}
.form-con .inp.textarea.size-s .inp-area{height: 48px;}

.form-con .main-career{position: relative; padding-bottom: 85px;}
.form-con .main-career .career-add-con{display: flex; align-items: center; justify-content: center; position: absolute; left: 0; bottom: 0; width: 100%; height: 61px; border-top: 1px solid #D8DDE2;}
.form-con .main-career .career-add-con button{display: flex; align-items: center; justify-content: center;}
.form-con .main-career .career-add-con button::before{content:""; display: block; margin-right: 6px; width: 20px; height: 20px; background: url("../img/common/ico_add_btn.svg") no-repeat center center / 100% auto;}
.form-con .main-career .inp-select{margin-top: 16px; width: 100%;}

.form-con .addr-select{width: 195px;}
.form-con .addr-select + .addr-select{margin-left: 12px;}

.form-con .form-title{margin-bottom: 16px;}
.form-con .form-title h3{font-size: 24px; font-weight: 700;}
.form-con .form-title h3 + p{margin-top: 12px;}
.form-con .form-title p{font-size: 14px; color: #7C828B;}

.form-con .title.mt{margin-top: 32px;}

@media all and (max-width:960px) {
  .contents.apply-form{max-width: none; width: 100%;}
  .form-con .content-box{padding: 32px 0 20px; box-shadow: none;}
  .form-con .main-career{padding-bottom: 0;}
  .form-con .main-career .career-add-con{position: relative; border-top: none;}
  .form-con .addr-select{width: calc(50% - 6px);}
}

@media all and (max-width:720px) {
  .contents.apply-form{padding: 0;}
  .contents.apply-form .page-tit{margin-bottom: 0; padding: 0 6.67vw;}

  .form-con .content-box{padding: 6.67vw 6.67vw; border-bottom: 4.44vw solid #F4F5F8; -webkit-border-radius: 0;-moz-border-radius: 0;border-radius: 0;}
  .form-con .content-box.border-none{border-bottom: none;}
  .form-con .content-box + .content-box{margin-top: 0;}
  .form-con .title{margin-bottom: 4.44vw;}
  .form-con .title h3{font-size: 4.44vw;}
  .form-con .title p{margin-left: 1.67vw; font-size: 3.33vw;}
  .form-con .desc-box{margin-bottom: 4.44vw; padding: 4.44vw; height: fit-content; border-radius: 2.78vw;}
  .form-con .notice-txt{padding-left: 1.67vw; font-size: 3.33vw;}
  .form-con .notice-txt::before{top: 1.81vw; width: 0.56vw; height: 0.56vw;}

  .form-con .inp-list .inp-checkbox-group > .inp-label{margin-bottom: 3.61vw;}
  .form-con .inp-list .checkbox-list{margin-bottom: -4.72vw;}
  .form-con .inp-list .checkbox-list ~ .inp.etc{margin-top: 3.33vw;}
  .form-con .inp-list .inp-checkbox .inp-label{margin-bottom: 4.72vw;}
  .form-con .inp-list .inp-radio-group > .inp-label{margin-bottom: 3.61vw;}
  .form-con .inp-list .radio-list{margin-bottom: -4.72vw;}
  .form-con .inp-list .inp-radio .inp-label{margin-bottom: 4.72vw;}
  .form-con .inp.size-l .inp-area{height: 16.11vw;}
  .form-con .inp-label{word-break: keep-all;}

  .form-con .inp-list .grid-col{grid-template-columns: auto;}
  .form-con .inp-address .inp-group.pc_only{display: none;}
  .form-con .inp-address .inp-group.mo_only{display: flex;}
  .form-con .inp-address .inp-group .inp-select{margin-right: 1.67vw;}
  .form-con .inp.textarea .inp-label .counter{font-size: 3.33vw;}
  .form-con .inp.textarea .inp-area{height: 31.11vw;}
  .form-con .inp.textarea.size-s .inp-area{height: fit-content;}

  .form-con .main-career{padding-bottom: 0;}
  .form-con .main-career .career-add-con{margin-top: 5.56vw; padding-bottom: 8.33vw; height: auto; font-size: 3.89vw;}
  .form-con .main-career .career-add-con button::before{margin-right: 1.11vw; width: 4.44vw; height: 4.44vw;}
  .form-con .main-career .inp-select{margin-top: 4.44vw;}

  .form-con .addr-select{width: calc(50% - 1.67vw);}
  .form-con .addr-select + .addr-select{margin-left: 3.33vw;}

  .form-con .page-btn{padding: 16px 6.67vw 0;}

  .form-con .form-title{margin-bottom: 6.67vw;}
  .form-con .form-title h3{font-size: 6.67vw;}
  .form-con .form-title h3 + p{margin-top: 1.67vw;}
  .form-con .form-title p{font-size: 3.33vw;}

  .form-con .title.mt{margin-top: 5.56vw;}
}

//share
.pop-wrap .share-list{@include flex('center');}
.pop-wrap .share-list li{text-align: center;}
.pop-wrap .share-list li ::before{content: ''; display: block; margin-bottom: 8px; width: 60px; height: 60px; border: 1px solid #EDEDED; border-radius: 50%; background-color: #F8F8F8; background-repeat: no-repeat; background-size: 100% auto; background-position: center;}
.pop-wrap .share-list li.facebook a::before{background-image: url("/assets/img/common/ico_facebook.svg");}
.pop-wrap .share-list li.kakao a::before{background-image: url("/assets/img/common/ico_kakao.svg");}
.pop-wrap .share-list li.twitter a::before{background-image: url("/assets/img/common/ico_twitter.svg");}
.pop-wrap .share-list li.copy a::before{background-image: url("/assets/img/common/ico_copy.svg");}
.pop-wrap .share-list li a{font-size: 14px; color: #7C828B;}
.pop-wrap .share-list li + li{margin-left: 32px;}

@media all and (max-width:720px) {
  .pop-wrap .share-list{justify-content: space-between;}
  .pop-wrap .share-list li ::before{margin-bottom: 1.67vw; width: 13.33vw; height: 13.33vw; border-width: 0.28vw;}
  .pop-wrap .share-list li a{font-size: 3.89vw;}
  .pop-wrap .share-list li + li{margin-left: 0;}
}
